<template>
  <div class="phone">
      <navbar title="拨打电话" :show-menu="true"></navbar>
      <div class="phoneinput">
          <span v-bind:class="{f12:this.num.length>=11, '': this.num.length< 11}">{{num}}</span>
          <i v-on:click="reducenum" v-if="this.num!=''"></i>
      </div>
      <div class="flex-container">
          <div class="flex-1" v-on:click="addnum">
              <p>1</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>2</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>3</p>
          </div>
      </div>
      <div class="flex-container">
          <div class="flex-1" v-on:click="addnum">
              <p>4</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>5</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>6</p>
          </div>
      </div>
      <div class="flex-container">
          <div class="flex-1" v-on:click="addnum">
              <p>7</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>8</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>9</p>
          </div>
      </div>
      <div class="flex-container last">
          <div class="flex-1" v-on:click="addnum">
              <p>*</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>0</p>
          </div>
          <div class="flex-1" v-on:click="addnum">
            <p>#</p>
          </div>
      </div>
      <div class="tcenter">
          <span class="phone_icon" v-on:click="callphone(num)"></span>
      </div>
      <div class="load2" v-if="showloding">
            <div class="loader">Loading...</div>
        </div>
  </div>
</template>

<script>
    import {
        getConfig,
        getItem
    } from 'Utils';
    import navbar from '../components/navbar'
    import http from 'Ajax';
    import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';

    export default {
        name: 'Callphone',
        components: {
            navbar
        },
        data() {
            return {
                num: '',
                showloding:false,
                iscalling:false,
                todothing:''
            };
        },
        methods: {
            addnum: function(event) {
                if (this.num.length == 13) {
                    return
                }
                this.num = this.num + event.target.outerText;
            },
            reducenum:function(){
                 this.num = this.num.substring(0,this.num.length-1)
            },
            callphone:function(num){
                if (this.iscalling == true) {
                    return false
                }
                // 打开遮罩
                this.showloding = true;
                // 拨号状态
                this.iscalling = true;
                // if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.num))) {
                //     MessageBox.alert('手机号码有误，请重填').then(action => {
                //         this.showloding = false;
                //     });
                //     return`1
                // }
                getConfig(config => {
                    http.get(config.main.call+getItem('id')+'/'+num).then(res2 => {
                        this.showloding = false;
                        this.iscalling = false;
                        var _this = this;
                        if(res2.data.code ==2){
                            MessageBox.alert('坐席身份不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==1){
                            MessageBox.alert('系统调用参数不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==3){
                            MessageBox.alert('主叫未接通').then(action => {
                            }); 
                        }else if(res2.data.code ==4){
                            MessageBox.alert('坐席通话中').then(action => {
                            }); 
                        }else if(res2.data.code ==0){
                            MessageBox.alert('拨号成功，请耐心等候').then(action => {
                                _this.showloding = true;
                                _this.iscalling = true;
                                
                                var toDo = function() {
                                    http.get(config.main.callback+res2.data.callid).then(res3 => {
                                         console.log(res3.errcode)
                                         if (res3.errcode == 0) {
                                            clearInterval(todothing);
                                            _this.iscalling = false;
                                            _this.showloding = false;
                                        } else {

                                        }
                                    })
                                };
                                var todothing = setInterval(toDo, 5000);


                            }); 
                        }
                        
                    })
                })
            }
        },
        mounted: function() {
        }
    }
</script>
<style lang="scss">
    @import '../styles/_reset.scss';
    .phone {
        
        .load2{
            background-color:rgba(0,0,0,0.5);
            position:absolute;
            top:0px;
            left:0px;
            width:100%;
            height:100%;
            opacity: 0.8;
        }
        .load2 .loader,
.load2 .loader:before,
.load2 .loader:after {
  border-radius: 50%;
}
.load2 .loader:before,
.load2 .loader:after {
  position: absolute;
  content: '';
}
.load2 .loader:before {
  width: 5.2em;
  height: 10.2em;
  background: #808080;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.2em 5.1em;
  transform-origin: 5.2em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.load2 .loader {
  font-size: 11px;
  text-indent: -99999em;
  margin: 13rem auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em #FFF;
}
.load2 .loader:after {
  width: 5.2em;
  height: 10.2em;
  background: #808080;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 5.1em;
  -webkit-transform-origin: 0px 5.1em;
  transform-origin: 0px 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
        .phoneinput {
            height: 70px;
            a{
                    color: #000;
                    text-decoration:none;
            }
            span {
                display: inline-block;
                border: none;
                width: 84%;
                height: 70px;
                line-height: 70px;
                font-size: 47px;
                color: #000;
                text-indent: 16px;
            }
            .f12{font-size:39px;}
            i {
                background-image: url('../../static/cutter_50.png');
                width: 23px;
                height: 13px;
                background-size: 23px 13px;
                display: inline-block;
                vertical-align: top;
                margin-top: 27px;
            }
        }
        .phone_icon {
            background-image: url('../../static/cutter_51.png');
            width: 70px;
            height: 70px;
            background-size: 70px;
            display: inline-block;
            margin-top: 3rem
        }
        .flex-container .flex-1 {
            height: 500%;
            line-height: 500%;
            text-align: center;
            border: 1px #ccc solid;
            border-left: none;
            border-bottom: none;
        }
        .flex-container.last {
            border-bottom: 1px #ccc solid;
        }
        .flex-container p {
            font-size: 40px;
            color: #000;
        }
    }
</style>


