<template>
  <div class="advice">
        <navbar title="意见反馈" :show-menu="true"></navbar>
       <mt-checklist
        class="page-part"
        v-model="value1"
        :options="options1">
        </mt-checklist>
        <textarea name="" id="" v-model="message" placeholder="请输入意见内容"></textarea>
        <span class="buttonsublime" v-on:click="sublimeform">提交</span>
  </div>
</template>

<script>
    import {
        getConfig,
        getItem
    } from 'Utils';
    import http from 'Ajax';
    import navbar from '../components/navbar'

    import {
        Checklist
    } from 'mint-ui';
    import 'mint-ui/lib/checklist/style.css';
        import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';


    export default {
        name: 'advice',
        components: {
            navbar,
            'mt-checklist':Checklist
        },
        data() {
            return {
                value1: [],
                message:''
            }
        },
        created() {
            this.options1 = [
                '功能简单易于操作',
                '功能复杂切不好用',
                '页面整洁美观',
                '页面不美观',
                // '提示清晰',
                // '提示不友好',
                // '系统流畅稳定',
                '系统异常频率高',
                '其他原因'
            ];
        },
        methods: {
            sublimeform:function(){
                getConfig(config => {
                    http.post(config.main.suggest+getItem('id'),{
                        label:this.value1,
                        context:this.message
                    }).then(res2 => {
                        if(res2.errcode==0){
                             MessageBox.alert('提交成功').then(action => {
                                 this.$router.push('/index');
                            });
                        }
                    })
                })
            }
        },
        mounted: function() {
        }
    }
</script>
<style lang="scss">
    @import '../styles/_reset.scss';
    .advice{
        textarea{
            width: 80%;
            height: 46px;
            background-color: #EFEFEF;
            text-indent: 10px;
            margin-left: 10%;
            border: none;
            margin-top: 13px;
            color: #999999;
        }
        .buttonsublime{
            border:2px #FFA800 solid;
            color:#FFA800;
            border-radius:2px;
            width:80%;
            height:30px;
            line-height:30px;
            text-align:center;
            font-size:18px;
            margin-left:10%;
            display:inline-block;
            margin-top:30px;
        }
    }
</style>


