<script>
    import {
        getConfig,
        gettoken,
        getItem,
        setItem
    } from 'Utils';


    import navbar from '../components/navbar'

    import Vue from 'vue';
    import {
        InfiniteScroll
    } from 'mint-ui';
    Vue.use(InfiniteScroll);

     import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';

    import http from 'Ajax';
    export default {
        name: 'Phonevideo',
        components: {
            navbar
        },
        data() {
            return {
                num: '',
                list: [],
                dialoginfo: {
                    id: '',
                    tel: ''
                },
                loading: true,
                isshowstatus: false,
                isshowdialog: false,
                currtsearch: 1,
                isActive:1,
                dateActive:1,
                phone:'',
                activeClass: 'active',
                hasdata:false,
                isshowsearch:false,
                sdate:new Date().Format("yyyy-MM-dd"),
                edate:new Date().Format("yyyy-MM-dd"),
                remark:'',
                showloding:false,
                iscalling:false,
                todothing:''
            };
        },
        watch: {
            // 如果 question 发生改变，这个函数就会运行
            sdate: function () {
                this.searchlist()
            },
            edate: function () {
                this.searchlist()
            },
            phone: function () {
                this.searchlist()
            }

        },
        methods: {
            closedialog:function(){
                this.isshowdialog= false;
                var body = document.body;
                body.classList.toggle('noscroll', false);
            },
            addnum: function(event) {
                if (this.num.length == 11) {
                    return
                }
                this.num = this.num + event.target.outerText;
            }, 
            fomatnum:function(num){
                if(num.length <=8){
                    return num
                }
                return num.substr(0,4) + '*'.repeat(num.length-8) +  num.substr(num.length-4)
            },
            showinfo:function(id,tel,name){
                this.isshowdialog= true;
                this.remark = '';
                this.dialoginfo.id = id;
                this.dialoginfo.tel = tel;
                this.dialoginfo.name = name;
                var body = document.body;
                body.classList.toggle('noscroll', true);
            },
            sublimeremark:function(){
                getConfig(config => {
                    
                    http.post(config.main.remark+this.dialoginfo.id,{
                        result:this.isActive,
                        remark:this.remark
                    }).then(res2 => {
                        if(res2.errcode == 0){
                            MessageBox.alert('反馈记录成功').then(action => {
                                this.isshowdialog= false;
                                this.searchlist()
                            });  
                        }
                    })
                })
                
            },
            today:function(edate,sdate,state){
                this.dateActive=state;
                this.sdate = sdate.Format("yyyy-MM-dd");
                this.edate = edate.Format("yyyy-MM-dd");
            },
            callphone:function(num){
                if (this.iscalling == true) {
                    return false
                }
                // 打开遮罩
                this.showloding = true;
                // 拨号状态
                this.iscalling = true;
                // if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.num))) {
                //     MessageBox.alert('手机号码有误，请重填').then(action => {
                //         this.showloding = false;
                //     });
                //     return`1
                // }
                getConfig(config => {
                    http.get(config.main.call+getItem('id')+'/'+num).then(res2 => {
                        this.showloding = false;
                        this.iscalling = false;
                        var _this = this;
                        if(res2.data.code ==2){
                            MessageBox.alert('坐席身份不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==1){
                            MessageBox.alert('系统调用参数不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==3){
                            MessageBox.alert('主叫未接通').then(action => {
                            }); 
                        }else if(res2.data.code ==4){
                            MessageBox.alert('坐席通话中').then(action => {
                            }); 
                        }else if(res2.data.code ==0){
                            MessageBox.alert('拨号成功，请耐心等候').then(action => {
                                _this.showloding = true;
                                _this.iscalling = true;
                                
                                var toDo = function() {
                                    http.get(config.main.callback+res2.data.callid).then(res3 => {
                                         console.log(res3.errcode)
                                         if (res3.errcode == 0) {
                                            clearInterval(todothing);
                                            _this.iscalling = false;
                                            _this.showloding = false;
                                        } else {

                                        }
                                    })
                                };
                                var todothing = setInterval(toDo, 5000);


                            }); 
                        }
                        
                    })
                })
            },
            loadMore() {

                this.loading = true;
                setTimeout(() => {
                    getConfig(config => {
                        gettoken().then(token =>{
                            Vue.http.get(config.openid.tastk+getItem('id')+'/'+this.pageindex+'?token='+token+'&sdate='+this.sdate+'&edate='+this.edate+'&tel='+this.phone).then(res => {
                                if (res.data.errcode == 9100) {
                                    setItem('token', '');
                                    gettoken();
                                }
                                res = res.data
                            if (res.errcode === 0) {
                                if (res.data.length > 0 && res.data.length==8) {
                                var _this = this;
                                res.data.forEach(function(data){
                                    _this.list.push(data);
                                })
                                this.pageindex++;
                                this.loading = false;
                                }else{
                                var _this = this;
                                res.data.forEach(function(data){
                                    _this.list.push(data);
                                });
                                this.loading = true;
                                }
                            } else {
                                alert(res.msg);
                            }
                            }, err => {
                                console.log(err);
                            });
                        });
                    });

                },500);
            },
            searchlist() {
                getConfig(config => {
                    gettoken().then(token =>{
                        Vue.http.get(config.openid.tastk+getItem('id')+'/0'+'?token='+token+'&sdate='+this.sdate+'&edate='+this.edate+'&content='+this.phone).then(res => {
                            if (res.data.errcode == 9100) {
                                    setItem('token', '');
                                    gettoken();
                                }
                            if (res.data.data.length > 0 && res.data.data.length==8) {
                                this.hasdata = false;
                                this.list = res.data.data;
                                this.pageindex = 1;
                                this.loading = false;
                            }else if(res.data.data.length == 0){
                                this.hasdata = true;
                                this.loading = true;
                                this.list = {};
                            }else{
                                this.list = res.data.data;
                                this.hasdata = false;
                                this.loading = true;

                            }
                            
                        },err =>{

                        })
                    })
                })
            }

        },
        mounted: function() {
            this.searchlist()
        }
    }
</script>
