            background-image: url('../../static/cutter_16.png');
            width: 45px;
            height: 45px;
            display: inline-block;
            background-size: 45px 45px;
            margin: 11px 0px 0px 0px;
            vertical-align: top;
            margin-left: 10px
        }
        .flex-2 .time {
            width: 56px;
            background-color: #72C114;
            border-radius: 34px;
            display: inline-block;
            color: #fff;
            height: 28px;
            line-height: 30px;
            margin-top: 20px;
            text-indent: 9px;
            i {
                width: 0px;
                height: 0px;
                border-bottom: 5px solid transparent;
                border-right: 5px solid transparent;
                border-top: 5px solid transparent;
                border-left: 5px #fff solid;
                display: inline-block;
                vertical-align: top;
                margin-top: 9px;
            }
        }
        .flex-2 .info {
            background-image: url('../../static/cutter_29.png');
            width: 30px;
            height: 30px;
            display: inline-block;
            background-size: 30px 30px;
            margin: 15px 0px 0px 5px;
            vertical-align: top;
            background-position: 0px 0px;
            
            background-repeat: no-repeat;
        }
        .tel {
            font-size: 15px;
    color: #333;
    font-weight: bold;
    line-height: 25px;
    text-indent: 12px;
    margin-top: 7px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 10rem;
            
        }
        .remark{
                width: 17rem;
            }
        .date {
            font-size: 14px;
            color: #999;
            text-indent: 11px;
            line-height:26px;
                letter-spacing: 1.1px;
        }
    }
    .tellist{
        z-index:1;
    }
}
</style>
<style>
    @media screen and (min-width: 310px) and (max-width: 321px) {
       .opportunties .search_right span{
            padding: 6px 2%;
        }
        .opportunties .phoneicon{
            width: 25px;
            height: 25px;
            background-size: 25px 25px;
            padding-right:8px;
        }
        .opportunties .list_item .flex-2 .info{
            width: 25px;
            height: 25px;
            background-size: 25px 25px;
        }
    }
</style>

<style>
    @media screen and (min-width: 500px) and (max-width: 740px) {
       .opportunties .dialogcontent{
            position: fixed;
            left: 10%;
            width: 80%;
            height: 80%;
            z-index: 100;
            background-color: #fff;
            top: 10%;
       }
       .opportunties .dialogcontent .callbutton{
               margin-top: -39px;
       }
       .opportunties .dialogcontent .emoji{
           margin: 12px 0px 0px 8px;
       }
       .opportunties .dialogcontent .remarkinput{
               margin-top: 4%;
       }
    }
</style>



