// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue/dist/vue.js'
// vuex
import store from './store/index'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource';
import App from './App.vue'
import {
    getConfig,
    setItem
} from 'Utils';


// import NProgress from 'vue-nprogress'
// Vue.use(NProgress)

// const nprogress = new NProgress({ parent: '.nprogress-container' })

// import VueProgressBar from 'vue-progressbar'

// Vue.use(VueProgressBar, {
//     color: 'rgb(143, 255, 199)',
//     failedColor: 'red',
//     height: '2px'
// })


Vue.use(VueRouter);
Vue.use(VueResource);


//页面模板
import home from './page/Home.vue'
import index from './page/Index.vue'
import callphone from './page/Callphone.vue'
import phonevideo from './page/Phonevideo.vue'
import aboutus from './page/Aboutus.vue'
import advice from './page/Advice.vue'
import statistics from './page/Statistics.vue'
import opportunities from './page/Opportunities.vue'
import msg from './page/Msg.vue'
import login from './page/Login.vue'




const router = new VueRouter({
    base: __dirname,
    routes: [
        { path: '/', name: 'index', component: index },
        { path: '/home', name: 'home', component: home },
        { path: '/index', name: 'index', component: index },
        { path: '/callphone', name: 'callphone', component: callphone },
        { path: '/phonevideo', name: 'phonevideo', component: phonevideo },
        { path: '/aboutus', name: 'aboutus', component: aboutus },
        { path: '/advice', name: 'advice', component: advice },
        { path: '/statistics', name: 'statistics', component: statistics },
        { path: '/opportunities', name: 'opportunities', component: opportunities },
        { path: '/msg', name: 'msg', component: msg },
        { path: '/login', name: 'login', component: login },
        { path: '*', component: index },
    ]
})

var vm = new Vue({
    router,
    // nprogress,
    render: h => h(App)
}).$mount('#app')


