<template>
  <div>
      <div class="top_bg">
            <nprogress-container></nprogress-container>
          <div class="flex-container">
              <div class="flex-1">
                  <img v-bind:src="headimg" alt="">
              </div>
              <div class="flex-4">
                  <p class="tel">{{data.tel}}</p>
                  <p class="address">{{data.address}}<font>({{data.shopname}})</font></p>
              </div>
          </div>
          <div class="flex-container topnum">
              <div class="flex-1">
                  <p class="font" v-if="!data.ispresale">回访总数</p>
                  <p class="font" v-if="data.ispresale">商机总数</p>
                  <p class="num">{{data.num}}</p>
              </div>
              <div class="flex-1">
                <p class="font">已完成</p>
                <p class="num">{{data.alnum}}</p>
              </div>
              <div class="flex-1">
                <p class="font">未完成</p>
                <p class="num">{{data.unnum}}</p>
              </div>
          </div>
      </div>
      <div class="icon_list">
        <div class="flex-container">
            <div class="flex-1">
                <div v-if="!data.ispresale">
                    <span class="icon icon1" v-bind:class="[{'icon2':!data.ispresale}]"></span>
                    <p>商机线索</p>
                </div>
                <router-link to="opportunities" v-if="data.ispresale">
                    <span class="icon icon1" v-bind:class="[{'icon2':!data.ispresale}]"></span>
                    <p>商机线索</p>
                </router-link>
            </div>
            <div class="flex-1">
                <div v-if="data.ispresale">
                    <span class="icon icon3" v-bind:class="[{'icon4':data.ispresale}]"></span>
                    <p>售后维系</p>
                </div>
                <router-link to="opportunities" v-if="!data.ispresale">
                    <span class="icon icon3" v-bind:class="[{'icon4':data.ispresale}]"></span>
                    <p>售后维系</p>
                </router-link>
            </div>
            <div class="flex-1">
                <router-link to="callphone">
                    <span class="icon icon5"></span>
                    <p>拨打电话</p>
                </router-link>
            </div>
        </div>
        <div class="flex-container">
            <div class="flex-1">
                <router-link to="msg">
                    <span class="icon icon6"></span>
                    <p>挂机短信</p>
                </router-link>
                
            </div>
            <div class="flex-1">
                <router-link to="phonevideo">
                    <span class="icon icon7"></span>
                    <p>电话录音</p>
                </router-link>
            </div>
            <div class="flex-1">
                <router-link to="statistics">
                    <span class="icon icon8"></span>
                    <p>历史统计</p>
                </router-link>
            </div>
        </div>
      </div>

      <div class="grey">

      </div>
      <div class="content_list">
          <div class="list_item flex-container" v-on:click='share'>
              <i class="icon share flex-1"></i>
              <div  class="flex-5">
                <p>分享</p>
              </div>
              <i class="icon back flex-1"></i>
          </div>
          <div class="list_item flex-container">
              <i class="icon info flex-1"></i>
              <div  class="flex-5">
                <router-link to="/aboutus"><p>关于我们</p></router-link>
              </div>
              <i class="icon back flex-1"></i>
          </div>
          <div class="list_item flex-container">
              <i class="icon advice flex-1"></i>
              <div  class="flex-5">
                <router-link to="/advice"><p>意见反馈</p></router-link>
              </div>
              <i class="icon back flex-1"></i>
          </div>
      </div>
  </div>
</template>

<script>
    import {
        getConfig,
        gettoken,
        setItem,
        getItem,
        wxshare,
        getUrlParams
    } from 'Utils';
    import http from 'Ajax';
    import NprogressContainer from 'vue-nprogress/src/NprogressContainer'

    
    import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';

    export default {
        name: 'index',
        components: {
            NprogressContainer
        },
        data() {
            return {
                show: true,
                headimg:'../../static/logo.png',
                data:{
                    tel:'',
                    address:'',
                    num:'',
                    alnum:'',
                    unnum:'',
                    id:'',
                    shopname:'',
                    ispresale:true
                    
                }
            };
        },
        methods: {
            share:function(){
                getConfig(config => {
                    wxshare(config.jssdk.link,config.jssdk.images,config.jssdk.title)
                })
                MessageBox.alert('分享请点击右上方的按钮').then(action => {
                }); 
            }
        },
        mounted: function() {
            // 通过微信openid来获取信息
            if(!!getUrlParams('openid') || !!getItem('openid')){ 
                getConfig(config => {
                    http.get(config.openid.userid+(getUrlParams('openid')||getItem('openid'))).then(res => {
                        if(res.data.length !=0){
                            this.data.tel = res.data.telephone;
                            this.data.address = res.data.store_name;
                            this.data.shopname = res.data.address;
                            this.data.id = res.data.id;
                            if(res.data.photo_path != null){
                                this.headimg = res.data.photo_path;
                            }
                            if(res.data.type == 1){
                                this.data.ispresale = true
                            }else{
                                this.data.ispresale = false
                            }
                        
                            setItem("id",res.data.id);
                            setItem("openid",(getUrlParams('openid')||getItem('openid')))
                            http.get(config.openid.totel+this.data.id).then(res2 => {
                                this.data.num = (res2.data.unfinishedCount + res2.data.completeCount) || 0
                                this.data.alnum = res2.data.completeCount || 0
                                this.data.unnum = res2.data.unfinishedCount  || 0
                            }, err => {
                                console.log(err);
                            });
                        }else{
                            MessageBox.alert('查不到用户数据，请稍微再试！').then(action => {
                                this.$router.push('/login');
                            }); 
                        }
                            
                    }, err => {
                        console.log(err);
                    });
                });
                // 通过登陆来获取用户信息
            }else if(getItem('id')!= undefined && getItem('id') == getItem('idinfo').id){
                var idinfo = getItem('idinfo')
                this.data.tel = idinfo.telephone;
                this.data.address = idinfo.store_name;
                this.data.shopname = idinfo.address;
                this.data.id = idinfo.id;
                if(idinfo.photo_path != null){
                    this.headimg = idinfo.photo_path;
                }
                if(idinfo.type == 1){
                    this.data.ispresale = true
                }else{
                    this.data.ispresale = false
                }
                getConfig(config => {
                    http.get(config.openid.totel+this.data.id).then(res2 => {
                        this.data.num = (res2.data.unfinishedCount + res2.data.completeCount) || 0
                        this.data.alnum = res2.data.completeCount || 0
                        this.data.unnum = res2.data.unfinishedCount  || 0
                    }, err => {
                        console.log(err);
                    });
                })
            }
            else{
                 this.$router.push('/login');
            }
            
        }

    }
</script>
<style lang="scss">
  @import '../styles/_reset.scss';
    .grey{
      width: 100%;
      height: 20px;
      background-color:#E8ECF0;
    }
    .content_list .list_item i{
        width: 16px;
        height: 16px;
        max-width: 16px;
        vertical-align: top;
        margin: 13px 16px 0px 13px;
    }
    .content_list .list_item .share {
        background-position: -139px -139px;
        margin: 14px 16px 0px 15px;
}
.info{    background-position: -155px -138px;
    min-width: 17px;
    min-height: 17px;}
.advice{    background-position: -172px -138px;
    min-width: 17px;
    min-height: 17px;}
    .content_list .list_item{
      width: 100%;
height: 45px;
border-bottom: 1px #F1F2F2 solid;
      .back{display: inline-block;background-image: url('../../static/cutter-1_23.png');height: 32px;
      width: 100%;background-repeat: no-repeat;    background-size: 64%;
    margin: 13px 10px 0px 5px;}
    a{
        text-decoration: none;
    }
      p{
        width: 100%;
height: 100%;
line-height: 45px;
color: #333;
font-size: 16px;
      }
    }
    .icon_list{height: 198px;}

    .icon{
      background-image: url('../../static/sprite.png');
      display: inline-block;
    }
    .icon_list{
      text-align: center;
    }
    .icon_list span{width: 45px;height: 45px}
    .icon1{
      background-position: 0px 0px;
    }
    .icon2{
      background-position: -47px 0px;
    }
    .icon3{
      background-position: -93px 0px;
    }
    .icon3{
      background-position: -93px 0px;
    }
    .icon4{
          background-position: -93px -46px;
    }
    .icon5{
              background-position: -139px 0px;
    }
    .icon6{

background-position: -185px 0px;
}
    .icon7{
                background-position: 0px -46px;
    }
    .icon8{
            background-position: -46px -46px;
    }
    .icon_list p{    font-size: 16px;
    color: #666;
    line-height: 30px;}
    .icon_list .flex-1{
        margin-top: 15px;
        a{
            text-decoration:none;
        }
    }
    .top_bg{
      width: 100%;
      height: 140px;
      background-image: url('../../static/bg_blue.png');
      a{
          text-decoration:none;
          color:#fff;
      }
      img{
        border-radius: 100px;
        border: 6px #55A6E9 solid;
        max-width: 50px;
        max-height: 50px;
            margin: 9px 11px 0px 13px;
      }
      .topnum .flex-1{height: 52px;border-right: 1px #51A3E5 solid;text-align: center;}

      .topnum .font{
        font-size: 17px;
color: #fff;
line-height: 32px;
      }
      .topnum .num{
        font-size: 22px;
        color: #D3FFFF;
      }
      .tel{
        font-size: 22px;
        color: #fff;
        margin-top: 13px;
        line-height: 37px;
      }
      .address{
        color: #fff;
        font-size: 13px;
        font-weight: bold;
        font{
          margin-left: 3px;
        }
      }
    }
    .nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  z-index: 2048;
  pointer-events: none;

  #nprogress {
    $color: #48e79a;

    .bar {
      background: $color;
    }
    .peg {
      box-shadow: 0 0 10px $color, 0 0 5px $color;
    }

    .spinner-icon {
      border-top-color: $color;
      border-left-color: $color;
    }
  }
}
</style>



