<template>
  <div class="statistics">
        <navbar title="历史统计" :show-menu="true"></navbar>
        <chart :type="'doughnut'" :data="pieData" ></chart>
        <div class="content_list">
          
            <router-link :to="{ name: 'phonevideo', params: { status: 0 }}">
                <div class="list_item flex-container">
                <i class="icon share flex-1"></i>
                <div  class="flex-4">
                    <p>成功清单</p>
                </div>
                <div class="flex-1">
                    <span>{{data[1]}}</span>
                    <i class="icon back"></i>
                </div>
                
                </div>
            </router-link>
              
          
          
             <router-link :to="{ name: 'phonevideo', params: { status: 1 }}">
                <div class="list_item flex-container">
                    <i class="icon info flex-1"></i>
                    <div  class="flex-4">
                        <p>失败清单</p>
                    </div>
                    <div class="flex-1">
                        <span>{{data[0]}}</span>
                        <i class="icon back"></i>
                    </div>
                    
                </div>
            </router-link>
              
          
      </div>
  </div>
</template>

<script>
    import {
        getConfig,
        getItem
    } from 'Utils';
    import http from 'Ajax';
    import navbar from '../components/navbar'
    import Chart from 'vue-bulma-chartjs'

    export default {
        name: 'statistics',
        components: {
            navbar,
            Chart
        },
        data() {
            return {
                labels: ['通话失败', '通话成功'],
                data: [1,1],
                options: {

                    scaleOverlay: true
                },
                backgroundColor: [
                    '#ED6A63',
                    '#5CBEAA'
                ]
            }
        },
        computed: {
            pieData () {
                return {
                    labels: this.labels,
                    datasets: [{
                    data: this.data,
                    backgroundColor: this.backgroundColor
                    }]
                }
            }
        },
        created () {
            getConfig(config => {
                http.get(config.main.statistics+getItem('id')).then(res2 => {
                    if(res2.data.callfail==null){
                        res2.data.callfail = 0
                    }
                    if(res2.data.callsuccess==null){
                        res2.data.callsuccess = 0
                    }
                    this.data[0] = res2.data.callfail
                    this.data[1] = res2.data.callsuccess
                    this.data = [res2.data.callfail,res2.data.callsuccess];
                }) 
            })
        },
        mounted: function() {
        }
    }
</script>
<style lang="scss">
    @import '../styles/_reset.scss';
    .statistics{
        a{
            text-decoration: none;
        }
        canvas{
            background-color:#E8ECF0;
            padding-top:20px;
            padding-bottom:30px;
        }
        .list_item span{
            display:inline-block;
            background-color:#F35125;
            width:20px;
            height:20px;
            border-radius: 20px;
            color:#fff;
            line-height:20px;
            text-align:center;font-size:12px;
            margin-top:13px;
        }
         .content_list .list_item i{
            width: 20px;
            height: 20px;
            max-width: 20px;
            vertical-align: top;
            margin: 13px 16px 0px 15px;
        }
        .content_list .list_item .share {
            background-image:url(../../static/cutter_70.png);
            background-size: 20px 20px;
            background-position:0px 0px;
            margin: 14px 16px 0px 15px;
        }
        .info{ 
            background-image:url(../../static/cutter_71.png);
            background-size: 20px 20px;
            background-position:0px 0px;
            margin: 14px 16px 0px 15px;
        }
        .content_list .list_item{
            width: 100%;
            height: 45px;
            border-bottom: 1px #F1F2F2 solid;
        .back{
            display: inline-block;
            background-image: url('../../static/cutter-1_23.png');
            height: 32px;
            width: 100%;
            background-repeat: no-repeat;    
            background-size: 64%;
            margin: 13px 0px 0px 0px;
        }
        p{
            width: 100%;
            height: 100%;
            line-height: 45px;
            color: #333;
            font-size: 16px;
            }
        
      }
    }
</style>


