<template>
  <div class="msg">
      <navbar title="挂机短信" :show-menu="true"></navbar>
      <p class="name2">最近一条通话记录：</p>
      <p class="list-item">
            <span class="labelfont" style=" letter-spacing: 3.5px;">被叫号码：</span>
            <span class="labelinfo">{{datas.calleenumber}}</span>
        </p>
        <p class="list-item">
            <span class="labelfont" style=" letter-spacing: 3.5px;">被叫时间：</span>
            <span class="labelinfo">{{datas.callstarttime}}</span>
        </p>

      <p class="name">请根据通话记录，自行决定是否需要发送短信</p>

      <textarea name="" id="" v-model="context" placeholder="请输入短信内容"></textarea>
        <span class="buttonsublime" v-on:click="postways">发送</span>
  </div>
</template>

<script>
    import {
        getConfig,
        getItem
    } from 'Utils';
    import http from 'Ajax';
    import navbar from '../components/navbar'

        import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';


    export default {
        name: 'Callphone',
        components: {
            navbar
        },
        data() {
            return {
                datas:{},
                context:''
            };
        },
        methods: {
            postways: function() {
                getConfig(config => {
                    http.post(config.openid.postmsg+getItem('id'),{
                        tophone:this.datas.calleenumber,
                        context:this.context
                    }).then(res2 => {
                        if(res2.errcode == 0){
                            MessageBox.alert('短信已经发出，即将返回首页').then(action => {
                                this.$router.push('/index');
                            });    
                        }
                        
                    }, err => {
                        console.log(err);
                    });
                });
            }
        },
        mounted: function() {
            
            this.searchlist()
            
        },
        mounted: function() {
            getConfig(config => {
                http.get(config.openid.lastrecall+getItem('id')).then(res2 => {
                    this.datas = res2.data;
                }, err => {
                    console.log(err);
                });
            });
        }
    }
</script>
<style lang="scss">
    @import '../styles/_reset.scss';
    .msg{
    textarea{
            width: 80%;
            height: 100px;
            background-color: #EFEFEF;
            text-indent: 10px;
            margin-left: 10%;
            border: none;
            margin-top: 13px;
            color: #999999;
            line-height: 26px;
        }
        .name{    font-size: 15px;
    text-align: center;
    line-height: 59px;
    color: #D04252;}
    .name2{
        line-height: 49px;
    text-indent: 14px;
    font-size: 15px;
    }
        .buttonsublime{
            border:2px #FFA800 solid;
            color:#FFA800;
            border-radius:2px;
            width:80%;
            height:30px;
            line-height:30px;
            text-align:center;
            font-size:18px;
            margin-left:10%;
            display:inline-block;
            margin-top:30px;
        }
        .list-item {
            line-height: 23px;
            margin-left: 12px;
            a{
                color:#333;
                text-decoration:none;
            }
            .labelfont {
                font-size: 14px;
                color: #999;
                width: 141px;
                display: inline-block;
                overflow: hidden;
            }
            .labelinfo {
                font-size: 16px;
                color: #333;
                display: inline-block;
                vertical-align: top;
                margin-left: -48px;
                font-weight: bold;
            }
        }
    }
</style>


