<template>
  <div class="login">
      <div class="login_bg">
            <div class="inputs">
                <div class="list">
                    <i class="loginicon"></i>
                    <input type="text" v-model="username">
                </div>

                <div class="list" style="border:none">
                    <i class="password"></i>
                    <input type="password" v-model="password">
                </div>
            </div>
      </div>
      <span class="loginbutton" v-on:click="loginpost">登录</span>
  </div>
</template>

<script>
    import {
        getConfig,
        getItem,
        setItem
    } from 'Utils';
    import navbar from '../components/navbar'
    import http from 'Ajax';
    import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';

    export default {
        name: 'Callphone',
        components: {
            navbar
        },
        data() {
            return {
                username:'',
                password:''
            };
        },
        methods: {
            addnum: function(event) {
                if (this.num.length == 11) {
                    return
                }
                this.num = this.num + event.target.outerText;
            },
            reducenum:function(){
                 this.num = this.num.substring(0,this.num.length-1)
            },
            loginpost:function(num){
                if (this.username=='') {
                    MessageBox.alert('请输入用户名').then(action => {
                    });
                    return
                }
                if (this.password=='') {
                    MessageBox.alert('请输入密码').then(action => {
                    });
                    return
                }
                getConfig(config => {
                    http.get(config.openid.login+this.username+'/'+this.password).then(res2 => {
                        if(res2.data==null){
                            MessageBox.alert('用户名或者密码错误').then(action => {
                            });
                        }else{
                            setItem('id',res2.data.id);
                            setItem('idinfo',res2.data);
                            this.$router.push('/index');
                        }
                    })
                })
            }
        },
        mounted: function() {
        }
    }
</script>
<style lang="scss">
    @import '../styles/_reset.scss';
    .login{
        .login_bg{
            background-image: url('../../static/bg.jpg');
            width: 100%;
            height: 100%;
            min-height: 100%;
            overflow: hidden;
            background-size: cover;
            position: fixed;
        }
        .loginbutton{
                position:absolute;
                bottom:40%;
                width:80%;
                left:10%;
                background: rgba(255,255,255,0.2);
                height:40px;
                line-height:40px;
                text-align:center;
                color:#fff;
                border-radius:10px;
        }
        .inputs{
            background: rgba(255,255,255,0.2);
            position: absolute;
            width: 80%;
            left: 10%;
            top: 30%;
            border-radius: 15px;
            
            .list{
                height: 56px;
                border-bottom: 1px #ccc solid;
            }
            input{
                    vertical-align: top;
    margin-top: 10px;
    border: none;
    background-color: transparent;
    color: #fff;
    height: 31px;
    line-height: 31px;
            }
        }
        .loginicon{
            background-image: url('../../static/loginicon.png');
            width: 30px;
            height: 30px;
            display: inline-block;
            background-size: 30px 30px;
            margin: 10px 4px 0px 12px;
            border-right: 1px #ccc solid;
            padding-right: 8px;
            background-repeat: no-repeat;
        }
        .password{
            background-image: url('../../static/passicon.png');
            width: 30px;
            height: 30px;
            display: inline-block;
            background-size: 30px 30px;
            margin: 10px 4px 0px 12px;
            border-right: 1px #ccc solid;
            padding-right: 8px;
            background-repeat: no-repeat;
        }
    }
    
</style>


