<script>
    import {
        getConfig,
        gettoken,
        getItem,
        registerjssdk,
        setItem
    } from 'Utils';

    import Vue from 'vue';
    import http from 'Ajax';
    import navbar from '../components/navbar';

    import {MessageBox} from 'mint-ui';
    import 'mint-ui/lib/message-box/style.css';

    import {
        InfiniteScroll
    } from 'mint-ui';
    Vue.use(InfiniteScroll);
    
    export default {
        name: 'Phonevideo',
        components: {
            navbar
            
        },
        data() {
            return {
                num: '',
                list: [{
                    date:[{
                        iscalling:false
                    }
                        
                    ]
                }
                ],
                dialoginfo: {
                    id: '',
                    tel: ''
                },
                loading:true,
                isshowstatus: false,
                isshowdialog: false,
                currtsearch: this.$route.params.status || 0,
                isActive:1,
                phone:'',
                activeClass: 'active',
                pageindex:0,
                hasdata:false,
                remark:'',
                showloding:false,
                iscalling:false,
                todothing:''
            };
        },
        watch: {
            // 如果 question 发生改变，这个函数就会运行
            currtsearch: function () {
                this.searchlist()
            },
            phone: function () {
                this.searchlist()
            }

        },
        methods: {
            closedialog:function(){
                this.isshowdialog= false;
                var body = document.body;
                body.classList.toggle('noscroll', false);
            },
            showinfo:function(id,tel,name){
                this.isshowdialog= true;
                this.remark = '';
                this.dialoginfo.id = id;
                this.dialoginfo.tel = tel;
                this.dialoginfo.name = name;
                var body = document.body;
                body.classList.toggle('noscroll', true);
            },
            callphone:function(num){
                if (this.iscalling == true) {
                    return false
                }
                // 打开遮罩
                this.showloding = true;
                // 拨号状态
                this.iscalling = true;
                // if (!(/^1[3|4|5|7|8]\d{9}$/.test(this.num))) {
                //     MessageBox.alert('手机号码有误，请重填').then(action => {
                //         this.showloding = false;
                //     });
                //     return`1
                // }
                getConfig(config => {
                    http.get(config.main.call+getItem('id')+'/'+num).then(res2 => {
                        this.showloding = false;
                        this.iscalling = false;
                        var _this = this;
                        if(res2.data.code ==2){
                            MessageBox.alert('坐席身份不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==1){
                            MessageBox.alert('系统调用参数不正确').then(action => {
                            }); 
                        }else if(res2.data.code ==3){
                            MessageBox.alert('主叫未接通').then(action => {
                            }); 
                        }else if(res2.data.code ==4){
                            MessageBox.alert('坐席通话中').then(action => {
                            }); 
                        }else if(res2.data.code ==0){
                            MessageBox.alert('拨号成功，请耐心等候').then(action => {
                                _this.showloding = true;
                                _this.iscalling = true;
                                
                                var toDo = function() {
                                    http.get(config.main.callback+res2.data.callid).then(res3 => {
                                         if (res3.errcode == 0) {
                                            clearInterval(todothing);
                                            _this.iscalling = false;
                                            _this.showloding = false;
                                        } else {

                                        }
                                    })
                                };
                                var todothing = setInterval(toDo, 5000);


                            }); 
                        }
                        
                    })
                })
            },
            sublimeremark:function(){
                getConfig(config => {
                    http.post(config.main.remark+this.dialoginfo.id,{
                        result:this.isActive,
                        remark:this.remark
                    }).then(res2 => {
                        if(res2.errcode == 0){
                            MessageBox.alert('反馈记录成功').then(action => {
                                this.isshowdialog= false;
                                this.searchlist()
                            });  
                        }
                    })
                })
                
            },
            loadMore() {
                this.loading = true;
                setTimeout(() => {
                    getConfig(config => {
                        gettoken().then(token =>{
                            Vue.http.get(config.main.list+getItem('id')+'/'+this.pageindex+'?token='+token+'&statue='+this.currtsearch+'&tel='+this.phone).then(res => {
                                if (res.data.errcode == 9100) {
                                    setItem('token', '');
                                    gettoken();
                                }
                                res = res.body;
                            if (res.errcode === 0) {
                                if (res.data.length > 0 && res.data.length==8) {
                                var _this = this;
                                res.data.forEach(function(data){
                                    _this.list[index].iscalling = false
                                    _this.list.push(data);
                                })
                                var _this = this;
                                this.pageindex++;
                                this.loading = false;
                                }else{
                                var _this = this;
                                res.data.forEach(function(data){
                                    _this.list[index].iscalling = false
                                    _this.list.push(data);
                                });
                                this.loading = true;
                                }
                            } else {
                                alert(res.msg);
                            }
                            }, err => {
                                console.log(err);
                            });
                        });
                    });

                },500);
            },
            play(src) {
                var src = src;
                var _this = this;
                this.list.forEach(function(data,index){
                    if(data.innercallid == src){
                        data.iscalling = true
                        Vue.set(_this.list,index,data)
                    }else{
                        data.iscalling = false
                        Vue.set(_this.list,index,data)
                    }
                })
                
                getConfig(config => {
                    http.get(config.main.getaddress+src).then(res2 => {
                        
                        if(res2.data.code==5){
                            src=""
                        }else{
                            src=res2.data.voiefilepath;
                            
                            var audio = document.getElementsByClassName('media-audio')[0];
                            audio.src = src
                            audio.play();
                            document.addEventListener("WeixinJSBridgeReady", function () {
                                audio.play();
                            }, false);
                            document.addEventListener('YixinJSBridgeReady', function() {
                                audio.play();
                            }, false);
                        }
                    }, err => {
                        console.log(err);
                    });
                });
            },
            formatSeconds(seconds) {
                var min = Math.floor(seconds / 60),
                    second = seconds % 60,
                    hour, newMin, time;

                if (min > 60) {
                    hour = Math.floor(min / 60);
                    newMin = min % 60;
                }

                if (second < 10) { second = '0' + second;}
                if (min < 10) { min = '0' + min;}

                return time = hour? (hour + ':' + newMin + ':' + second) : (min + ':' + second);
            },
            searchlist(){
                getConfig(config => {
                    gettoken().then(token =>{
                        Vue.http.get(config.main.list+getItem('id')+'/0'+'?token='+token+'&statue='+this.currtsearch+'&tel='+this.phone).then(res => {
                                if (res.data.errcode == 9100) {
                                    setItem('token', '');
                                    gettoken();
                                    this.searchlist();
                                }
                                
                            if (res.data.data.length > 0 && res.data.data.length==8) {
                                this.hasdata = false;
                                this.list = {};
                                this.list = res.data.data;
                                var _this = this;
                                this.list.forEach(function(data,index){
                                    _this.list[index].iscalling = false
                                })
                                console.log(this.list)
                                this.pageindex = 1;
                                this.loading = false;
                            }else if(res.data.data.length == 0){
                                this.hasdata = true;
                                this.loading = true;
                                this.list = {};
                            }else{
                                this.list = {};
                                this.list = res.data.data;
                                var _this = this;
                                this.list.forEach(function(data,index){
                                    _this.list[index].iscalling = false
                                })
                                this.hasdata = false;
                                this.loading = true;
                            }

                        },err =>{

                        })
                    })
                })
            }

        },
        mounted: function() {
            this.searchlist();

            // 微信特殊处理
            function audioAutoPlay(id){
                var audio = document.getElementById(id);
                document.addEventListener("WeixinJSBridgeReady", function () {
                        audio.play();
                }, false);
                document.addEventListener('YixinJSBridgeReady', function() {
                    audio.play();
                }, false);
            }
            audioAutoPlay('Jaudio');
        }
    }
</script>
