                display: inline-block;
                vertical-align: top;
                margin-top: 9px;
                float:right;
                margin-right:3px;
            }
            span{
                font-size: 12px;
                color: #fff;
                display: inline-block;
                vertical-align: top;
                margin-top: 0px;
                position: absolute;
                margin-right: 13px;
                width: 2px;
            }
        }
        span{
            height: 24px;
            display: inline-block;
            font-size: 23px;
            margin-top: 20px;
            vertical-align: top;
            color:#E4E5E6;
        }
        .flex-2 .info {
            background-image: url('../../static/cutter_29.png');
            width: 28px;
            height: 28px;
            display: inline-block;
            background-size: 28px 28px;
            margin: 17px 0px 0px 0px;
            vertical-align: top;
            background-position: 0px 0px;
            background-repeat: no-repeat;
        }
        .tel {
            font-size: 17px;
            color: #333;
            font-weight: bold;
            line-height: 35px;
            text-indent: 12px;
            margin-top: 4px;
        }
        .date {
            font-size: 14px;
            color: #999;
            text-indent: 11px;
        }
    }
    }
</style>

<style>
    @media screen and (min-width: 310px) and (max-width: 321px) {
       .opportunties .search_right span{
            padding: 6px 2%;
        }
        .Phonevideo .search_right input{
            width: 80%;
            max-width: 160px;
        }
        .Phonevideo .list_item .flex-2 .time{
                width: 60px;
    line-height: 27px;
        }
        .Phonevideo .list_item .flex-2 .info{
            width: 22px;
            height: 22px;
            background-size: 22px 22px;
                margin-top: 21px;
        }
    }
</style>

<style>
    @media screen and (min-width: 500px) and (max-width: 700px) {
       .Phonevideo .dialogcontent{
            position: fixed;
            left: 10%;
            width: 80%;
            height: 80%;
            z-index: 100;
            background-color: #fff;
            top: 10%;
       }
       .Phonevideo .dialogcontent .callbutton{
               margin-top: -39px;
       }
       .Phonevideo .dialogcontent .emoji{
           margin: 12px 0px 0px 8px;
       }
       .Phonevideo .dialogcontent .remarkinput{
               margin-top: 4%;
       }
    }
</style>


